<template>
  <div>
    <div id="app">
      <canvas ref="threejs" class="threejs" :class="grabbing" />
    </div>
    <div
      ref="productoverlay"
      id="product-overlay"
      @click="this.removeProductInfoOverlay"
    >
      <h3>Name: {{ this.selectedProductInfo.name }}</h3>
      <!-- TODO: remove soon after DEMO -->
      <h3>Zoomed scale: {{ this.selectedProductInfo.zoomedScale }}</h3>
      <h3>Far scale: {{ this.selectedProductInfo.farScale }}</h3>
      <a>{{ this.selectedProductInfo.link }}</a>
    </div>
  </div>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";

/** This is the config for where to show the other sphere (where we click to change the scene) */
const envConfig = {
  1: [
    {
      name: "STEP2.jpg",
      step: 2,
      position: { x: 28, y: -3, z: -3.5 },
    },
    {
      name: "STEP3.jpg",
      step: 3,
      position: { x: 28, y: -4, z: 22 },
    },
  ],
  2: [
    {
      name: "STEP1.jpg",
      step: 1,
      position: { x: -28, y: -3, z: 3 },
    },
    {
      name: "STEP3.jpg",
      step: 3,
      position: { x: -12, y: -6, z: 40 },
    },
  ],
  3: [
    {
      name: "STEP1.jpg",
      step: 1,
      position: { x: -30, y: -4, z: -25 },
    },
    {
      name: "STEP2.jpg",
      step: 2,
      position: { x: 15, y: -7, z: -50 },
    },
  ],
};

// TODO (optional) add a lookat vector for each background (optional)

/** The productSlots are the positions in each img where we are placing the products that we wanna show */
const stepConfig = [
  {
    name: "STEP1.jpg",
    step: 1,
    productSlots: [
      { x: 70, y: -5, z: 2 },
      { x: 50, y: -5, z: 4 },
      { x: 15, y: -1, z: 5 },
    ],
  },
  {
    name: "STEP2.jpg",
    step: 2,
    productSlots: [
      { x: 7, y: -5, z: 15 },
      { x: 5, y: -5, z: 11 },
      { x: 15, y: -5, z: 12 },
    ],
  },
  {
    name: "STEP3.jpg",
    step: 3,
    productSlots: [
      { x: 7, y: -5, z: -15 },
      { x: 5, y: -5, z: -11 },
      { x: 15, y: -5, z: -12 },
    ],
  },
];

/** dummy objects representing what we will get from the API */
const glbObjects = [
  {
    url: "/8afb282373834deda783f685c450f4c9.glb",
    productInfo: {
      name: "Dachpfanne rot G10 PV",
      link: "someLink1",
      zoomedScale: 3,
      farScale: 5,
    },
  },
  {
    url: "/8afb282373834deda783f685c450f4c9.glb",
    productInfo: {
      name: "Dachpfanne rot G10 PV",
      link: "someLink2",
      zoomedScale: 2,
      farScale: 8,
    },
  },
  {
    url: "/8afb282373834deda783f685c450f4c9.glb",
    productInfo: {
      name: "Dachpfanne rot G10 PV",
      link: "someLink3",
      zoomedScale: 1,
      farScale: 10,
    },
  },
];

// TODO add zoomedScale and farScale that will be retrieved from backend and set by admin
// these are REQUIRED props of each given object

export default {
  name: "App",
  data() {
    return {
      backgroundSrc: "STEP3.jpg",
      step: 3,
      scene: null,
      camera: null,
      renderer: null,
      loader: null,
      controls: null,
      light: null,
      raycaster: [],
      mouse: null,
      envConfig,
      stepConfig,
      controlledObject: null,
      mouseX: 0,
      mouseY: 0,
      grabbing: "",
      backgroundSphere: null,
      objects: [],
      selectedProductInfo: { name: null, link: null },
    };
  },

  methods: {
    init() {
      this.scene = new THREE.Scene();
      const windowAspect = window.innerWidth / window.innerHeight;
      const camera = new THREE.PerspectiveCamera(75, windowAspect, 0.1, 10000);
      camera.position.x = 1;
      camera.lookAt(1, 0, 4);
      this.camera = camera;

      this.declareRenderer();
      const controls = new OrbitControls(this.camera, this.renderer.domElement);
      controls.enableZoom = false;

      const mouse = new THREE.Vector2();
      const raycaster = new THREE.Raycaster();

      // binding the variables to the VM instance variables (to the "this" variables)
      [this.controls, this.mouse, this.raycaster] = [
        controls,
        mouse,
        raycaster,
      ];

      this.addLight();

      const loader = new GLTFLoader();
      this.loader = loader;

      // a very far away position (dummy) to start with for loading an object
      const dummyPosition = { x: 10000, y: 10000, z: 10000 };

      // load the given array of objects sent by the API
      glbObjects.forEach((obj, index) =>
        this.loadGLBObject(obj, dummyPosition, index)
      );

      // TODO: maybe add a light from the camera or from origin towards what we see, check with Dawid
      this.renderShowroom();

      this.addAxesHelpers();

      this.renderer.domElement.addEventListener(
        "click",
        this.onDocumentMouseClick,
        false
      );
      this.renderer.domElement.addEventListener(
        "mousemove",
        this.onMouseMove,
        false
      );
      window.addEventListener("resize", this.onWindowResize, false);
    },

    /** helper function to load GLB objects  */
    loadGLBObject(object, objectPosition, objectOrder) {
      this.loader.load(object.url, (obj) => {
        // The following is only useful if we enable the zoom in the scene
        // this.setMaxZoom(obj.scene)

        obj.scene.position.set(
          objectPosition.x,
          objectPosition.y,
          objectPosition.z
        );

        // attach the given product info to the object
        obj.scene.productInfo = object.productInfo;

        // TODO this shall be dynamic too like the placeholder coordinates
        // scale here is some arbitary value, we can control it later or pass it with coordinates
        const farScale = object.productInfo.farScale;
        obj.scene.scale.set(farScale, farScale, farScale);

        // add the loaded obj to the scene
        this.scene.add(obj.scene);

        // add the obj to loaded objects
        this.objects[objectOrder] = obj.scene;

        // adjust the positions of all loaded objects
        this.setObjectsPositions();
      });
    },
    /** Sets the camera max zoom based on a given object (only useful if zoom is enabled) */
    setMaxZoom(objScene) {
      const coords = new THREE.Box3()
        .setFromObject(objScene)
        .getSize(new THREE.Vector3());
      const maxCoord = Math.max.apply(null, Object.values(coords));
      this.controls.minDistance = maxCoord;
    },
    /** axes helpers to show us x,y,z axes in the showroom */
    addAxesHelpers() {
      // add XYZ axes helpers
      // The X axis is red. The Y axis is green. The Z axis is blue.
      const axesHelper = new THREE.AxesHelper(10);
      this.scene.add(axesHelper);

      const dir = new THREE.Vector3(1, 2, 0);
      //normalize the direction vector (convert to vector of length 1)
      dir.normalize();

      const origin = new THREE.Vector3(0, 0, 0);
      const length = 1;
      const hex = 0xffff00;

      const arrowHelper = new THREE.ArrowHelper(dir, origin, length, hex);
      this.scene.add(arrowHelper);
    },
    addLight() {
      this.light = new THREE.PointLight(0xffffff, 2, 100);
      this.light.position.set(5, 5, 5);
      this.scene.add(this.light);
    },
    createMark({ name, position }) {
      const geometry = new THREE.SphereGeometry(2, 32, 32);
      const material = new THREE.MeshPhongMaterial({
        color: 0xffffff,
        opacity: 1,
        transparent: true,
      });
      const mesh = new THREE.Mesh(geometry, material);

      mesh.name = name;
      mesh.position.x = position.x;
      mesh.position.y = position.y;
      mesh.position.z = position.z;
      this.scene.add(mesh);
    },
    renderShowroom() {
      const textureLoader = new THREE.TextureLoader();
      const texture = textureLoader.load(this.backgroundSrc);
      const material = new THREE.MeshBasicMaterial({
        map: texture,
      });
      material.side = THREE.DoubleSide;

      const fbxLoader = new FBXLoader();
      if (!this.backgroundSphere) {
        fbxLoader.load("/SPHEREforIVANFBXCP.fbx", (obj) => {
          obj.traverse((node) => {
            if (node.isMesh) {
              node.material = material;
            }
          });
          this.backgroundSphere = obj;
          this.scene.add(obj);
        });
      } else {
        const obj = this.scene.getObjectByName("Sphere");
        obj.traverse((node) => {
          if (node.isMesh) {
            node.material = material;
          }
        });
      }

      // After changing the background, adjust the objs positions to suit the new scene
      this.setObjectsPositions();
      // Create the marks for camera position change (background change)
      envConfig[this.step].forEach((el) => this.createMark(el));
    },

    /** Get the object to the center */
    flyTheObjectInTheMiddle(obj) {
      this.changeObjectPosition(obj, { x: 0, y: 0, z: 0 });
      // TODO (optional) put a scroll that controls the scale for the user
      // controls how big the obj is when clicked
      const zoomedScale = obj.productInfo.zoomedScale;
      obj.scale.set(zoomedScale, zoomedScale, zoomedScale);
    },

    /** on obj click, show info box */
    // TODO filter the objects by the object name we hit and then get the info of that object
    showProductInfo(objectBody) {
      // set selected productinfo
      this.selectedProductInfo = objectBody.productInfo;
      // show product info
      this.$refs.productoverlay.style.display = "block";
    },

    removeProductInfoOverlay() {
      this.$refs.productoverlay.style.display = "none";
    },

    /** loop on array of objects in the scene and adjust their positions */
    setObjectsPositions() {
      const currentStep = stepConfig.find((a) => a.step === this.step);

      this.objects.forEach((obj, index) => {
        this.changeObjectPosition(obj, currentStep.productSlots[index]);
        // reset the scales (also can be an admin given value for scale on the table for example)
        const scale = obj.productInfo.farScale;
        obj.scale.set(scale, scale, scale);
      });
    },
    declareRenderer() {
      this.renderer = new THREE.WebGLRenderer({
        antialias: true,
        canvas: this.$refs.threejs,
      });
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.renderer.setClearColor(0xffffff);
    },
    changeBackground(src) {
      envConfig[this.step].forEach((el) => {
        const obj = this.scene.getObjectByName(el.name);
        this.scene.remove(obj);
      });
      this.backgroundSrc = src;
      this.step = stepConfig.find((el) => el.name === src).step;
      this.renderShowroom();
    },
    changeObjectPosition(obj, position) {
      obj.position.set(position.x, position.y, position.z);
    },
    animate() {
      requestAnimationFrame(this.animate);
      this.controls.update();
      this.render();
    },

    /** Handle browser window resize */
    onWindowResize() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();

      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },
    onDocumentMouseClick(event) {
      event.preventDefault();
      this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
      this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

      this.raycaster.setFromCamera(this.mouse, this.camera);
      let items = this.raycaster.intersectObjects(this.scene.children, true);

      // TODO We should have some pattern to filter the objects here, maybe some prefix to all their names for example
      // using find to get the first element here, we can use filter to get array of matching elements
      const itemHit = items.find((item) => item.object.name.includes("SF"));

      // using find instead of filter to get the first matching element
      const backgroundSphereHit = items.find((item) =>
        item.object.name.includes("STEP")
      );

      this.setClickedObj(itemHit);
      this.backgroundSphereClicked(backgroundSphereHit);
    },

    /** Change background when user click on another step */
    backgroundSphereClicked(backgroundSphereHit) {
      if (backgroundSphereHit != undefined) {
        this.scene.remove(backgroundSphereHit.object.name);
        this.changeBackground(backgroundSphereHit.object.name);
      }
    },

    /**
     * Set selected object if the user clicks (i.e. anything other than the background sphere)
     *
     * If he clicks out of the objects, then clear selected object
     * */
    setClickedObj(itemHit) {
      // if the user click on some object (i.e. anything other than the background sphere)
      if (itemHit != undefined) {
        // TODO: maybe there is a better way to grab the object clicked from this array, not just the first obj we see

        // we set the target to be the obj parent as the obj is a group of children of the glb file
        // so the parent obj here is the correct glb file that we want to orbit around
        const objectBody = itemHit.object.parent;

        // Fly the object near to us
        this.flyTheObjectInTheMiddle(objectBody);
        this.showProductInfo(objectBody);

        // When you click on an object, we mark this as the selected object
        this.controlledObject = objectBody;
        this.grabbing = "grabbing";
      } else {
        // here, if the person clicked outside of the objects:
        // First: we adjust the previously selected obj to original orientation
        this.resetObjXOrientation(this.controlledObject);
        this.resetObjYOrientation(this.controlledObject);
        this.removeProductInfoOverlay();
        // Then: and we reset the controlled object to null
        this.controlledObject = null;
        this.grabbing = "";

        // return all objs to their original positions in that scene
        this.setObjectsPositions();
      }
    },

    onMouseMove(event) {
      event.preventDefault();
      const deltaX = event.clientX - this.mouseX;
      const deltaY = event.clientY - this.mouseY;

      this.mouseX = event.clientX;
      this.mouseY = event.clientY;

      if (this.controlledObject != null) {
        this.controlledObject.rotation.y += deltaX / 100;
        this.controlledObject.rotation.x += deltaY / 100;
      }
    },
    resetObjXOrientation(obj) {
      if (obj != null) {
        obj.rotation.x = 0;
      }
    },
    resetObjYOrientation(obj) {
      if (obj != null) {
        obj.rotation.y = 0;
      }
    },
    render() {
      this.renderer.render(this.scene, this.camera);
    },

    // TODO try to mark all the assets as cachable (probably from the backend when retrieving the assets)
    // TODO rethink this func as it helps call the imgs but apparently not really load them and reuse them in the scene

    /** silently preload the remaining backgrounds (scenes) */
    loadRemainingScenes() {
      const remainingBackgrounds = this.stepConfig.filter(
        (el) => el.name !== this.backgroundSrc
      );
      const textureLoader = new THREE.TextureLoader();
      remainingBackgrounds.forEach((el) => textureLoader.load(el.name));
    },
  },
  mounted() {
    this.init();
    this.animate();
    this.loadRemainingScenes();
  },
};
</script>

<style>
body {
  margin: 0;
  padding: 0;
}
.controls {
  position: absolute;
  z-index: 3;
  top: 20px;
  left: 20px;
}

/* (Optional) Apply a "closed-hand" cursor during drag operation. */
.grabbing {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

#product-overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 20%;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  cursor: pointer;
  color: white;
}
</style>
